<template>
  <div class="change-status">
    <div>
      <el-steps :active="active" finish-status="success" align-center>
        <el-step :title="step1"></el-step>
        <el-step :title="step2"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <div class="tip" v-if="bindStatus === 'B02'">{{tipText}}</div>
      <template v-if="bindStatus === 'B01'">
        <div class="iconClass el-icon-time"></div>
        <div class="iconTip">处理中（<span>{{countDownNum}}</span> s）</div><br/>
        <div class="iconTip">{{iconTipText}}</div>
      </template>
      <template v-if="bindStatus === 'B02'">
        <div class="iconClass el-icon-circle-check-outline"></div>
        <div class="iconTip">成功</div>
      </template>
      <template v-if="bindStatus === 'B03'">
        <div class="iconClass el-icon-warning"></div>
        <div class="iconTip">失败</div>
      </template>
      <el-table
        :data="tableData"
        border
        style="width: 100%" v-if="bindStatus !== 'B01'">
        <el-table-column
          prop="shoukuanzhanghu"
          label="收款账户" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="zizhanghao"
          label="子帐号" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="zhanghuleixing"
          label="账户类型"
          align="center">
        </el-table-column>
        <el-table-column
          prop="renzhengzhuangtai"
          label="认证状态"
          align="center">
        </el-table-column>
        <el-table-column
          prop="yinhangkahao"
          label="银行卡账号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="yinhangkakaihuhang"
          label="银行卡开户行"
          align="center">
        </el-table-column>
      </el-table>
      <el-button type="primary" style="margin-top: 30px;" @click="goDetails">查看账户详情</el-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
import APIURL from '@/config/APIURL'
Axios.defaults.timeout = 5000
Axios.defaults.withCredentials = true
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=utf-8'
Vue.prototype.$ajax = Axios
export default {
  name: 'change-status',
  data () {
    return {
      active: 3,
      bindStatus: '',
      countDownNum: 5,
      step1: '',
      step2: '',
      tipText: '',
      iconTipText: '',
      tableData: [{
        shoukuanzhanghu: '',
        zizhanghao: '',
        zhanghuleixing: '',
        renzhengzhuangtai: '',
        yinhangkahao: '',
        yinhangkakaihuhang: ''
      }]
    }
  },
  created () {
    this.chengeTypeSet()
    this.queryMerchantDepositAcctInfo()
  },
  methods: {
    chengeTypeSet () {
      if (this.$route.query.type === 'yinhangka') {
        console.log('yinhangka')
        this.step1 = '填写银行卡信息'
        this.step2 = '变更银行卡'
        this.tipText = '重要提示：银行卡变更后，请注意认证状态，以免影响后续放款和提现操作。'
        this.iconTipText = '您的华夏银行存管账户换卡申请已经提交，请耐心等待开户结果。'
      } else if (this.$route.query.type === 'shoujihao') {
        console.log('shoujihao')
        this.step1 = '填写手机号码'
        this.step2 = '变更手机号'
        this.tipText = ''
        this.iconTipText = '您的华夏银行存管账户变更手机号申请已经提交，请耐心等待处理结果。'
      } else if (this.$route.query.type === 'kaihu') {
        console.log('kaihu')
        this.step1 = '填写银行卡信息'
        this.step2 = ' 确认开户'
        this.tipText = ''
        this.iconTipText = ' 您的华夏银行存管账户开户申请已经提交，请耐心等待开户结果。注意：开户成功后，您需要通过绑定的银行卡账户向您的存管账户完成一笔任意金额的转账操作，还请提前准备。'
      }
    },
    goDetails () {
      this.$router.push({ path: '/' })
    },
    countDownNumFun () {
      let timer = setInterval(() => {
        this.countDownNum--
        if (this.countDownNum === 0) {
          clearInterval(timer)
          this.$router.push({ path: '/' })
        }
      }, 1000)
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    },
    queryMerchantDepositAcctInfo () {
      const queryMerchantDepositAcctInfoUrl = APIURL.queryMerchantDepositAcctInfo
      this.$ajax.post(queryMerchantDepositAcctInfoUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        recoCode: sessionStorage.getItem('recoCode'),
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          this.bindStatus = res.data.data.bindStatus
          if (res.data.data.bindStatus === 'B02') {
            // 绑卡成功
            this.tableData[0].shoukuanzhanghu = res.data.data.depositAcct
            this.tableData[0].zizhanghao = res.data.data.subAcct
            this.tableData[0].zhanghuleixing = res.data.data.businessTypeMsg
            this.tableData[0].renzhengzhuangtai = res.data.data.acctStsMsg
            this.tableData[0].yinhangkahao = res.data.data.bankCardNo
            this.tableData[0].yinhangkakaihuhang = res.data.data.openAcctBankName
          } else if (res.data.data.bindStatus === 'B03') {
            // 绑卡失败
            this.tableData[0].shoukuanzhanghu = res.data.data.depositAcct
            this.tableData[0].zizhanghao = res.data.data.subAcct
            this.tableData[0].zhanghuleixing = res.data.data.businessTypeMsg
            this.tableData[0].renzhengzhuangtai = res.data.data.acctStsMsg
            this.tableData[0].yinhangkahao = res.data.data.bindExcBankCardNo
            this.tableData[0].yinhangkakaihuhang = res.data.data.bindExcBankName
            this.countDownNumFun()
          }
        } else {
          alert(res.data.msg)
        }
      })
    }
  },
  watch: {
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .change-status{
    div{
      line-height: 1.2;
      text-align: right;
    }
    .iconClass{
      display: block;
      font-size: 80px;
      margin: 0 auto;
      text-align: center;
    }
    .el-icon-time{
      color: #FF9224;
    }
    .el-icon-circle-check-outline{
      color: #28FF28;
    }
    .el-icon-warning{
      color: #FF0000;
    }
    .iconTip{
      text-align: center;
    }
    .el-steps{
      padding: 20px 0;
    }
    .tip{
       width: 80%;
       height: 30px;
       background-color: #1890ff;
       color: #fff;
       margin: 0 auto;
       text-align: center;
       line-height: 30px;
     }
    .el-table{
      margin-top: 30px;
    }
    .el-button{
      display: block;
      margin: 0 auto;
    }
  }
</style>



